* {
  box-sizing: border-box;
}

.movieinfo {
  display: grid;
  grid-template-columns: 320px auto;
  max-width: 800px;
  column-gap: 15px;
  margin: 0 auto;
  margin-top: 100px;
  justify-content: center;
  background-color: white;
  border-radius: 25px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

h1 {
  text-align: center;
  margin-top: 40px;
  text-decoration: none;
}

a {
  text-decoration: none;
  color: rgb(20, 15, 85);
}

a:hover {
  text-decoration: underline;
}

img {
  display: block;
  margin: 30px;
  margin-left: -40px;
  width: 100%;
  max-width: 300px;
  position: relative;
}

@media screen and (max-width: 640px) {
  .movieinfo {
    display: block;
    margin-top: 30px;
  }
  img {
    margin: 0 auto;
    position: relative;
  }
  .textInfo {
    margin-left: 40px;
  }
}

.textInfo {
  color: rgb(25, 12, 73);
  margin-top: 40px;
  margin-bottom: 40px;
}
